@import "../utils.scss";

.wrapper {
  display: flex;
  width: auto; // => column ÏÉÅÏóêÏÑú width: 100% Ïù¥Í±∞Ïì∞Î©¥ Î¨∏Ï†úÍ∞Ä ÏÉùÍ∏¥Îã§..
  color: black;
  padding: 0 32px;
  // background-color: $oc-pink-1;
  height: 74px;
  align-items: center;
  border-bottom: 1px solid $oc-gray-4;
  @include media("<medium") {
    padding: 32px 0;
    flex-direction: column;
    height: 100%;
    //background-color: $oc-violet-7;
  }
}
/*
  small: 376px,
  medium: 768px,
  large: 1024px,
  huge: 1200px
*/

.functionsList {
  list-style-type: none;
  margin: 0;
  padding: 0;
  border: 0;
  flex: 1;
  // background-color: $oc-violet-3;
  li {
    float: left;
    padding: 0 15px;
  }
  @include media("<medium") {
    li {
      clear: both;
      width: 100%;
      text-align: center;
      padding: 15px 0;
    }
  }
}

.logo {
  height: inherit;
  @include media("<=medium") {
    height: 74px;
  }
  img {
    height: inherit;
  }
}

